import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'

import AOS from 'aos';
import 'aos/dist/aos.css';

function Products() {

  AOS.init();

  const [products, setProducts] = useState([])
  const [modal, setModal] = useState(false)
  const [editData,setEditData] = useState({})
  const [category, setCategory] = useState([])

  useEffect(() => {
    fetch("http://localhost:7000/products")
      .then(res => res.json())
      .then(data => setProducts(data))

    fetch("http://localhost:7000/categories")
    .then(res => res.json())
    .then(data => setCategory(data))
  }, [])


  const deleteProduct = (id) => {
    fetch(`http://localhost:7000/products/${id}`, {
      method: "DELETE",
      headers: {
        "content-type": "application/json"
      }
    }).then(res => res.json())
      .then(data => {
        setProducts(products.filter(e => e.id !== id))
      })
  }

  const editProduct = (id) => {
    let data = products.find(item => item.id === id)
    setEditData(data)
    setModal(!modal)


  }

  const handleInput = (e) => {
    const {name, value} = e.target 
    setEditData({...editData, [name]: value})
  }
  
  const handleSubmit = (e) => {
    e.preventDefault()
    fetch(`http://localhost:7000/products/${editData.id}`,{
      method: "PUT",
      headers: {
        "Content-Type": "Application/json"
      },
      body: JSON.stringify(editData)
    })
    .then(res => res.json())
    .then(data => {
      let list = [...products]
      let index = list.findIndex(e => e.id === editData.id)
      list.splice(index, 1, editData)
      setProducts(list)
      setModal(!modal)
    })
  }
  return (
    <>
      <div className='container py-3'>
        <h1>Products</h1>
        <div className="row">
          {
            products.length > 0 ?
              products.map((index, key) => (
                <div className="col-6 mt-2" data-aos="fade-up" key={key}>
                  <div className="card">
                    <img className="card-img-top" src={index.image} alt="Card image cap" />
                    <div className="card-body">
                      <h5 className="card-title">{index.title}</h5>
                      <div className="btn-group">
                        <Link to={`/products/${index.id}`} className="btn btn-primary">Read More</Link>
                        <button className='btn btn-danger' onClick={() => deleteProduct(index.id)}>Delete</button>
                        <button className='btn btn-success' onClick={() => editProduct(index.id)}>Edit</button>
                      </div>
                    </div>
                  </div>
                </div>
              )) :
              <h1 className='text-center'>No item</h1>
          }
        </div>
      </div>
      <div className={modal ? "modal fade show" : "modal fade "} >
        <div className="modal-dialog modal-dialog-centered" >
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" >Modal title</h5>
              <button type="button" className="close" onClick={() => setModal(!modal)}>
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <form onSubmit={(e) => handleSubmit(e)}>
                <input type="text" 
                  className='form-control mt-2' 
                  placeholder='Title' name='title' 
                  value={editData.title || ""}
                  onChange={(e) => handleInput(e)} 
                />
                <input type="text" 
                  className='form-control mt-2' 
                  placeholder='Price' name='price' 
                  value={editData.price || ""}
                  onChange={(e) => handleInput(e)} 
                />
                <input type="text" 
                  className='form-control mt-2' 
                  placeholder='Desc' name='description' 
                  value={editData.description || ""}
                  onChange={(e) => handleInput(e)} 
                />
                <select name="category" 
                className='form-control mt-2' 
                value={editData.category || ""}
                onChange={(e) => handleInput(e)}>
                  {
                    category.map((index, key) => (
                      <option value={index.name} key={key}>{index.name}</option>
                    ))
                  }
                </select>
                <input type="text" 
                className='form-control mt-2' 
                placeholder='Image' name='image' 
                value={editData.image || ""}
                onChange={(e) => handleInput(e)}
                />
                <button className="btn mt-2 btn-success btn-block">Edit</button>
              </form>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn btn-secondary" onClick={() => setModal(!modal)}>Close</button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default Products